* {
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 60px;
}

input{
  width: 100%;
  font-size: 1.2rem;
  color: gray;
  padding: 5px;
}

input::placeholder{
  font-size: 1rem;
  font-style: italic;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin: 0 10px 10px 0;
}

.card p{
  padding: 5px 5px 8px;
  text-align: center;
  max-width: 170px;
  margin: auto;
  font-size: 0.7rem;
}

.card img{
  max-height: 170px;
}

ol{
  padding: 0;
}